
* {
  box-sizing: border-box;
  color: inherit;
  font-family: Helvetica, 'Source Han Sans', 'Microsoft JhengHei', sans-serif;
  font-size: 24px;
  line-height: 1;
  text-align: center;
}
body {
  color: #434A54;
  margin: 0 auto;
  max-width: 36em;
  min-width: 24em;
  position: relative;
  width: 100%;
}
.button-set {
  display: table;
  width: 100%;
}
.button-cell {
  display: table-cell;
  padding: 0.25em;
}
button {
  border: none;
  border-radius: 5px;
  color: #FFF;
  padding: 0.5em 0.75em;
  width: 100%;
}
button#accept {
  background: #8CC152;
}
button#accept.active {
  background: #A0D468;
}
button#reject {
  background: #DA4453;
}
button#reject.active {
  background: #ED5565;
}
button#dismiss {
  background: #3BAFDA;
}
button#dismiss.active {
  background: #4FC1E9;
}
button[disabled] {
  background: #BBB !important;
}
article {
  padding: 1em;
}
article#status-box {
  border-bottom: #DDD 1px solid;
}
article#status-box.offline {
  color: #DA4453;
}
article#message-box {
  height: 8em;
}
article#station-box {
    height: 4em
}
.tablet {
background-color: #BBB;
height: 3em;
width: 3em;
margin: 1em;
display: inline-block;
text-align: center;
}
.tablet.free {
background-color: #A0D468;
}
.tablet.lock {
background-color: #FFD95F;
}



